import React from 'react';
import {Button, Divider, Form, Input, Modal, Space,message} from "antd";
import {useDispatch} from "react-redux";
import {postHospitalSetAsync} from "@/store/slices/hospital";

const validateMessages = {
    required: '请输入${label}'
};

const layout = {
    labelCol: { span: 6 },
    wrapperCol: { span: 16 }
};

const HospitalSetModel = (props:any) => {
    const dispatch:any = useDispatch();
    const onFinish = async (values: any) => {
        try{
            await dispatch(postHospitalSetAsync(values));
            message.success(`添加${values.hosname}成功！`)
            handleCancel();
        }catch(e:any){
            message.error(e.message);
        }

    };
    const handleCancel = () => {
        props.setIsModalOpen(false);
        onReset();
    };
    const [form] = Form.useForm();
    const onReset = () => {
        form.resetFields();
    };
    return (
        <>
            <Modal title="添加医院"
                   open={props.isModalOpen}
                   onCancel={handleCancel}
                   centered
                   footer={null}
                   mask={true}
            >
                <Divider />
                <Form {...layout} form={form} name="nest-messages" onFinish={onFinish} validateMessages={validateMessages}>
                    <Form.Item name='hosname' label="医院名称"
                               rules={[{ required: true,
                                   min:4,
                                   max:20,
                                   message:"请输入4-20字数之间的医院名称！"
                               }]}>
                        <Input />
                    </Form.Item>
                    <Form.Item name='hoscode' label="医院编号" rules={[{ required: true }]}>
                        <Input />
                    </Form.Item>
                    <Form.Item name='apiUrl' label="api基础地址"
                               rules={[{ required: true,
                                   pattern:/^https?:\/\/(([a-zA-Z0-9_-])+(\.)?)*(:\d+)?(\/((\.)?(\?)?=?&?[a-zA-Z0-9_-](\?)?)*)*$/i,
                                   message:"请输入正确的URL地址！"
                               }]}>
                        <Input />
                    </Form.Item>
                    <Form.Item name='contactsName' label="联系人姓名" rules={[{ required: true}]}>
                        <Input />
                    </Form.Item>
                    <Form.Item name= 'contactsPhone' label="联系人手机"
                               rules={[{ required: true,
                                   pattern:/^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/,
                                   message:"请输入正确的手机号！"
                               }]}>
                        <Input />
                    </Form.Item>
                    <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 6 }}>
                        <Space >
                            <Button type="primary" htmlType="submit">提交</Button>
                            <Button htmlType="button" onClick={onReset} >重置</Button>
                        </Space>
                    </Form.Item>
                </Form>
            </Modal>
        </>
    );
};

export default HospitalSetModel;